.contacts {
  &__title {
    text-align: center;
    position: relative;

    &:before {
      @include center_x();
      content: '';
      height: 1px;
      width: 720px;
      background: map_get($color, accent);
      bottom: -15px;
      position: absolute;

      @include set_media(small_tablet) {
        width: 400px;
        bottom: -12px;
      }

      @include set_media(mobile) {
        width: 290px;
        bottom: -5px;
      }
    }
  }

  &__list {
    display: flex;
    justify-content: center;
    //margin-top: 45px;
  }

  &__employee {
    color: map_get($color, title);
    margin: 0 25px;

    @include set_media(large) {
      margin: 0 13px;
    }

    &-photo {
      position: relative;
      margin-bottom: 15px;

      & img {
        max-width: 100%;
      }

      @include set_media(large) {
        max-width: 160px;
      }
    }

    &-socials {
      position: absolute;
      display: flex;
      bottom: 0;
      right: 0;
    }

    & h4 {
      font-size: 1.87rem;

      @include set_media(large) {
        font-size: 1.375rem;
      }

      @include set_media(mobile) {
        font-size: 1.1rem;
      }
    }

    & p {
      font-size: 1rem;

      @include set_media(large) {
        font-size: 0.625rem;
      }
    }
  }

  &__form {
    text-align: center;
    margin-top: 45px;

    & .input-wrapper {

      @include set_media(small_tablet) {
        width: 100%;
        margin-top: 10px;
      }
    }

    &-toggle {
      display: block;
      margin: 35px auto 0;

      @include set_media(mobile) {
        margin-top: 25px;
      }
    }
  }

  &__user-info {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    & .input-wrapper {

      @include set_media(small_tablet){
        max-width: calc(50% - 10px);
      }

      @include set_media(mobile){
        max-width: 100%;
      }

      &:not(:last-child) {
        margin-right: 20px;

        @include set_media(mobile) {
          margin-right: 0;
        }
      }
    }


    }

    &__input {
      width: 290px;

      @include set_media(large) {
        width: 260px;
      }

      @include set_media(small_tablet) {
        width: 100%;
      }
    }

    &__message {
      @extend .input;
      width: 600px;
      height: 120px;
      margin-top: 25px;
      resize: none;

      @include set_media(large) {
        width: 540px;
      }

      @include set_media(small_tablet) {
        width: 100%;
      }

      @include set_media(mobile) {

        margin-top: 0;
      }

      & + .input-error.show {
        bottom: calc(100% - 40px);


      }
    }

    &__action {
      width: 260px;
      margin: 25px auto 0;

      & button {
        width: 100%;
      }
    }

    &__addresses {
      display: flex;
      justify-content: center;
      margin-top: 45px;
    }

    &__item {
      max-width: 250px;
      color: map-get($color, title);
      padding: 5px 0;

      @include set_media(mobile) {
        max-width: 30%;
      }

      &:not(:last-child) {
        margin-right: 5px;
      }

      &-details {
        padding-left: 20px;
      }

      & h4 {
        font-size: 1.25rem;
        font-weight: 600;

        @include set_media(mobile) {
          font-weight: 300;
          color: get_color(accent);
          font-size: 0.69rem;
        }
      }

      & p {
        margin-top: 10px;
        font-size: 0.93rem;
        font-weight: 300;
        position: relative;

        @include set_media(large) {
          font-size: 0.75rem;
        }

        @include set_media(mobile) {
          font-size: 0.6rem;
        }

        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: -20px;
        }

        &.location {
          &:before {

            background: url("../img/location.svg") no-repeat;
            width: 15px;
            height: 20px;
            background-size: contain;

            @include set_media(large) {
              width: 13px;
              height: 17px;
            }
          }
        }

        &.email {
          &:before {
            @include center_y();
            background: url("../img/mail.svg") no-repeat;
            width: 15px;
            height: 10px;
            background-size: cover;
          }
        }
      }
    }
  }

  
